import Image from "next/image";

const features = [
    {
        title: "Smart Learning System",
        description:
            "Manage schools, programs, courses, study materials, enrollment, bookmark... Support categorizing study materials by school, program, semester, etc.",
        icon: "🎓",
    },
    {
        title: "Advanced Search & Filter",
        description:
                "Tìm kiếm theo từ khóa, bộ lọc theo trường, chương trình, khóa học, độ khó, loại tài liệu, v.v.",
        icon: "🔍",
    },
    {
        title: "Personal Rating, Comment, Bookmark",
        description:
            "Users can rate, comment, bookmark, create personal folders, reminders, etc.",
        icon: "⭐",
    },
    {
        title: "Dashboard & Learning Analytics",
        description:
            "Analytics of activities, learning progress, top contributors, trending materials, etc.",
        icon: "📊",
    },
];

export default function UniqueFeaturesSection() {
    return (
        <section className="w-full bg-white py-20 px-4 md:px-0">
            <div className="max-w-6xl mx-auto flex flex-col md:flex-row items-center gap-12">
                {/* Left: Text */}
                <div className="flex-1">
                    <h2 className="text-3xl md:text-4xl font-bold mb-4 text-gray-900">
                        Nền tảng chia sẻ tài liệu học tập <span className="text-blue-600">toàn diện</span>
                    </h2>
                    <p className="text-gray-600 mb-8 text-lg">
                        Website cung cấp hệ sinh thái học thuật hiện đại, giúp sinh viên dễ dàng tìm kiếm, chia sẻ, lưu trữ và đánh giá tài liệu học tập theo trường, chương trình, khóa học. Tối ưu cho trải nghiệm học tập và cộng đồng sinh viên tại Canada.
                    </p>
                </div>
                {/* Right: SVG Illustration */}
                <div className="flex-1 flex justify-center items-center">
                    <img
                        src="/feature.svg"
                        alt="Unique features illustration"
                        className="w-full max-w-md h-auto drop-shadow-xl"
                        loading="lazy"
                    />
                </div>
            </div>
            {/* Features List */}
            <div className="max-w-4xl mx-auto mt-16 grid grid-cols-1 md:grid-cols-2 gap-8">
                {features.map((feature) => (
                    <div
                        key={feature.title}
                        className="flex items-start gap-4 bg-[#f6fafd] rounded-xl p-6 shadow-sm hover:shadow-md transition"
                    >
                        <span className="text-3xl md:text-4xl">{feature.icon}</span>
                        <div>
                            <h3 className="font-semibold text-lg text-gray-900 mb-1">{feature.title}</h3>
                            <p className="text-gray-600 text-base">{feature.description}</p>
                        </div>
                    </div>
                ))}
            </div>
        </section>
    );
} 